import { ADD_TO_CART } from "../Actions/types";
import { ADD_AMOUNT } from "../Actions/types";
import { SUBSTRACT_AMOUNT } from "../Actions/types";
import letMeData from "../../Products/LetMeIn/LetMeInData";

export default function(state = letMeData, action) {
  switch (action.type) {
    case ADD_TO_CART:
      const product = state.find(item => item.id === action.payload);
      const stateIndex = state.indexOf(product);
      state[stateIndex].inCart = true;
      state[stateIndex].amount = 1;
      console.log({ state });
      state[stateIndex].total = parseFloat(
        (
          state[stateIndex].amount *
          (state[stateIndex].isOnPromo
            ? state[stateIndex].promotion
            : state[stateIndex].price)
        ).toFixed(2)
      );

      return [...state];
    case ADD_AMOUNT:
      const addProduct = state.find(item => item.id === action.payload);
      const addIndex = state.indexOf(addProduct);
      state[addIndex].amount += 1;
      state[addIndex].total = parseFloat(
        (
          state[addIndex].amount *
          (state[addIndex].isOnPromo
            ? state[addIndex].promotion
            : state[addIndex].price)
        ).toFixed(2)
      );
      return [...state];
    case SUBSTRACT_AMOUNT:
      const substractProduct = state.find(item => item.id === action.payload);
      const substractIndex = state.indexOf(substractProduct);
      state[substractIndex].amount -= 1;
      state[substractIndex].total = parseFloat(
        (
          state[substractIndex].amount *
          (state[substractIndex].isOnPromo
            ? state[substractIndex].promotion
            : state[substractIndex].price)
        ).toFixed(2)
      );
      return [...state];
    default:
      return state;
  }
}
