import { KERMIT_ADD_TO_CART } from "../Actions/kermitTypes";
import { KERMIT_ADD_AMOUNT } from "../Actions/kermitTypes";
import { KERMIT_SUBSTRACT_AMOUNT } from "../Actions/kermitTypes";
import kermitData from "../../Products/Kermit/KermitData";

export default function(kermitState = kermitData, action) {
  switch (action.type) {
    case KERMIT_ADD_TO_CART:
      console.log({ kermitState });
      const product = kermitState.find(item => item.id === action.payload);
      const stateIndex = kermitState.indexOf(product);
      kermitState[stateIndex].inCart = true;
      kermitState[stateIndex].amount = 1;
      kermitState[stateIndex].total = parseFloat(
        (
          kermitState[stateIndex].amount *
          (kermitState[stateIndex].isOnPromo
            ? kermitState[stateIndex].promotion
            : kermitState[stateIndex].price)
        ).toFixed(2)
      );
      return [...kermitState];
    case KERMIT_ADD_AMOUNT:
      const addProduct = kermitState.find(item => item.id === action.payload);
      const addIndex = kermitState.indexOf(addProduct);
      kermitState[addIndex].amount += 1;
      kermitState[addIndex].total = parseFloat(
        (
          kermitState[addIndex].amount *
          (kermitState[addIndex].isOnPromo
            ? kermitState[addIndex].promotion
            : kermitState[addIndex].price)
        ).toFixed(2)
      );
      return [...kermitState];
    case KERMIT_SUBSTRACT_AMOUNT:
      const substractProduct = kermitState.find(
        item => item.id === action.payload
      );
      const substractIndex = kermitState.indexOf(substractProduct);
      kermitState[substractIndex].amount -= 1;
      kermitState[substractIndex].total = parseFloat(
        (
          kermitState[substractIndex].amount *
          (kermitState[substractIndex].isOnPromo
            ? kermitState[substractIndex].promotion
            : kermitState[substractIndex].price)
        ).toFixed(2)
      );
      return [...kermitState];
    default:
      return kermitState;
  }
}
