import React from "react";
import "./FirstWorldMeme.css";
import ProductPrice from "../MemeList/ProductPrice";

function FirstWorldMeme(props) {
  return (
    <div className="col-12 col-md-6 col-xl-4 mt-5 meme-template d-flex justify-content-center justify-content-md-start">
      <div className="firstWrld-meme">
        <img
          src={props.item.img}
          alt="Img meme"
          className="firstWrldImg img-fluid"
        />
        <h4 className="text1">{props.item.text1}</h4>
        <h4 className="text2">{props.item.text2}</h4>
      </div>
      <ProductPrice item={props} />
    </div>
  );
}

export default FirstWorldMeme;
