import React, { Component } from "react";
import { connect } from "react-redux";
import "./CSS/Cart.css";
import CartRender from "./ComponentsRender/CartRender";
import { addAmount } from "./Store/Actions/letMeCartActions";
import { substractAmount } from "./Store/Actions/letMeCartActions";
import { PikachuAddAmount } from "./Store/Actions/pikachuActions";
import { PikachuAubstractAmount } from "./Store/Actions/pikachuActions";
import { KermitAddAmount } from "./Store/Actions/kermitActions";
import { KermitAubstractAmount } from "./Store/Actions/kermitActions";
import { FirstWrldAddAmount } from "./Store/Actions/firstWrldActions";
import { FirstWrldAubstractAmount } from "./Store/Actions/firstWrldActions";

class Cart extends Component {
  render() {
    const letMeCartRender = this.props.letMe.map(item => (
      <CartRender
        key={item.id}
        item={item}
        addAmount={() => this.props.addAmount(item.id)}
        substractAmount={() => this.props.substractAmount(item.id)}
      />
    ));

    const pikaCartRender = this.props.pika.map(item => (
      <CartRender
        key={item.id}
        item={item}
        addAmount={() => this.props.PikachuAddAmount(item.id)}
        substractAmount={() => this.props.PikachuAubstractAmount(item.id)}
      />
    ));

    const kermitCartRender = this.props.kermit.map(item => (
      <CartRender
        key={item.id}
        item={item}
        addAmount={() => this.props.KermitAddAmount(item.id)}
        substractAmount={() => this.props.KermitAubstractAmount(item.id)}
      />
    ));

    const firstWrldCartRender = this.props.firstWrld.map(item => (
      <CartRender
        key={item.id}
        item={item}
        addAmount={() => this.props.FirstWrldAddAmount(item.id)}
        substractAmount={() => this.props.FirstWrldAubstractAmount(item.id)}
      />
    ));

    const letMeMap = this.props.letMe.map(item => item.total);
    const LetMePrice = parseFloat(
      letMeMap.reduce((sum, item) => sum + item).toFixed(2)
    );
    const pikachuMap = this.props.pika.map(item => item.total);
    const pikachuPrice = parseFloat(
      pikachuMap.reduce((sum, item) => sum + item).toFixed(2)
    );
    const kermitMap = this.props.kermit.map(item => item.total);
    const kermitPrice = parseFloat(
      kermitMap.reduce((sum, item) => sum + item).toFixed(2)
    );
    const firstWrldMap = this.props.firstWrld.map(item => item.total);
    const firstWrldPrice = parseFloat(
      firstWrldMap.reduce((sum, item) => sum + item).toFixed(2)
    );

    return (
      <div className="container-fluid bg-secondary cartDiv">
        <h1 className="text-center">Your cart</h1>
        <div className="d-flex flex-column-reverse flex-lg-row">
          <div className="row justify-content-center justify-content-lg-start">
            {letMeCartRender}
            {pikaCartRender}
            {kermitCartRender}
            {firstWrldCartRender}
          </div>
          <section className="col-2 border border-info align-self-center align-self-lg-start mb-3 summary-div">
            <h2 className="text-center">Summary for memes:</h2>
            <div>
              <div className="d-flex justify-content-between align-items-center">
                <h5>Let me in</h5>
                <span className="h3">$ {LetMePrice}</span>
              </div>
              <div className="d-flex justify-content-between align-items-center">
                <h5 className="">Suprised Pikachu</h5>
                <span className="h3">$ {pikachuPrice} </span>
              </div>
              <div className="d-flex justify-content-between align-items-center">
                <h5>Dark side Kermit</h5>
                <span className="h3">$ {kermitPrice} </span>
              </div>
              <div className="d-flex justify-content-between align-items-center">
                <h5>First world problems</h5>
                <span className="h3">$ {firstWrldPrice}</span>
              </div>
            </div>
            <div className="">
              <h2 className="text-center">Summary:</h2>
              <div className="d-flex justify-content-between align-items-center">
                <h2>Total: </h2>
                <h2>
                  ${" "}
                  {parseFloat(
                    (
                      LetMePrice +
                      pikachuPrice +
                      kermitPrice +
                      firstWrldPrice
                    ).toFixed(2)
                  )}
                </h2>
              </div>
              <div className="text-right my-2">
                <a
                  href="https://www.youtube.com/watch?v=dQw4w9WgXcQ"
                  target="_blank"
                >
                  <button className="btn btn-danger">Buy now!</button>
                </a>
              </div>
            </div>
          </section>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  letMe: state.letMeData,
  pika: state.pikachuData,
  kermit: state.kermitData,
  firstWrld: state.firstWrldData
});

const mapDispatchToProps = dispatch => {
  return {
    addAmount: id => {
      dispatch(addAmount(id));
    },
    substractAmount: id => {
      dispatch(substractAmount(id));
    },
    PikachuAddAmount: id => {
      dispatch(PikachuAddAmount(id));
    },
    PikachuAubstractAmount: id => {
      dispatch(PikachuAubstractAmount(id));
    },
    KermitAddAmount: id => {
      dispatch(KermitAddAmount(id));
    },
    KermitAubstractAmount: id => {
      dispatch(KermitAubstractAmount(id));
    },
    FirstWrldAddAmount: id => {
      dispatch(FirstWrldAddAmount(id));
    },
    FirstWrldAubstractAmount: id => {
      dispatch(FirstWrldAubstractAmount(id));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Cart);
