import React from "react";
import "./List.css";
import { Link } from "react-router-dom";

export default function List(props) {
  return (
    <Link
      to={props.item.link}
      className="col-lg-3 col-sm-5 col-12 productListDiv d-flex justify-content-center pt-2"
    >
      <img
        src={props.item.img}
        alt="Img"
        className="list-img img-fluid align-self-center"
      />
      <h6 className="memeTitle text-center">{props.item.title}</h6>
    </Link>
  );
}
