import { FIRST_WORLD_ADD_TO_CART } from "./firstWrldTypes";
import { FIRST_WORLD_ADD_AMOUNT } from "./firstWrldTypes";
import { FIRST_WORLD_SUBSTRACT_AMOUNT } from "./firstWrldTypes";

export const FirstWrldAddToCart = id => {
  return {
    type: FIRST_WORLD_ADD_TO_CART,
    payload: id
  };
};

export const FirstWrldAddAmount = id => {
  return {
    type: FIRST_WORLD_ADD_AMOUNT,
    payload: id
  };
};

export const FirstWrldAubstractAmount = id => {
  return {
    type: FIRST_WORLD_SUBSTRACT_AMOUNT,
    payload: id
  };
};
