import React, { Component } from "react";
import FirstWorldMeme from "./FirstWorldMeme";
import { connect } from "react-redux";
import { FirstWrldAddToCart } from "../../Store/Actions/firstWrldActions";

class FirstWorldRender extends Component {
  render() {
    const firstWrldRender = this.props.firstWrld.map(item => (
      <FirstWorldMeme
        key={item.id}
        item={item}
        handleClick={() => this.props.FirstWrldAddToCart(item.id)}
      />
    ));
    return (
      <div className="container-fluid">
        <div className="row">{firstWrldRender}</div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  firstWrld: state.firstWrldData
});

const mapDispatchToProps = dispatch => {
  return {
    FirstWrldAddToCart: id => {
      dispatch(FirstWrldAddToCart(id));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FirstWorldRender);
