import React, { Component } from "react";
import KermitMeme from "./KermitMeme";
import { connect } from "react-redux";
import { KermitAddToCart } from "../../Store/Actions/kermitActions";

class KermitRender extends Component {
  render() {
    const kermitRender = this.props.kermit.map(item => (
      <KermitMeme
        key={item.id}
        item={item}
        handleClick={() => this.props.KermitAddToCart(item.id)}
      />
    ));
    return (
      <div className="container-fluid">
        <div className="row">{kermitRender}</div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  kermit: state.kermitData
});

const mapDispatchToProps = dispatch => {
  return {
    KermitAddToCart: id => {
      dispatch(KermitAddToCart(id));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(KermitRender);
