import { combineReducers } from "redux";
import letMeReducer from "./letMeReducer";
import pikachuReducer from "./pikachuReducer";
import kermitReducer from "./kermitReducer";
import firstWrldReducer from "./firstWrldReducer";

export default combineReducers({
  letMeData: letMeReducer,
  pikachuData: pikachuReducer,
  kermitData: kermitReducer,
  firstWrldData: firstWrldReducer
});
