import React from "react";
import "./List.css";
import classNames from "classnames";

export default function productPrice(props) {
  return (
    <div
      id="priceFrame"
      className="d-flex flex-column justify-content-end align-items-end border border-info border-width"
    >
      {console.log(props)}
      {props.item.item.isOnPromo ? (
        <div className="h-100 w-100 d-flex align-items-center justify-content-center">
          <p
            id="promo-text"
            className="text-danger border border-danger rounded p-2 mb-1"
          >
            HOT DEAL
          </p>
        </div>
      ) : null}
      {props.item.item.isOnPromo ? (
        <h4 className="text-right mr-1 mb-1">
          {" "}
          Promotion price:{" "}
          <p className="text-danger font-weight-bold mb-1">
            <span>$</span>
            {props.item.item.promotion}
          </p>
        </h4>
      ) : null}
      <h4
        className={classNames(
          "mr-1 mb-0 price",
          props.item.item.isOnPromo ? "" : "no-promo"
        )}
      >
        Price:
      </h4>{" "}
      {props.item.item.isOnPromo ? (
        <h4 className="mr-1 mb-0 price">
          <p style={{ textDecoration: "line-through" }}>
            <span>$</span>
            {props.item.item.price}
          </p>
        </h4>
      ) : (
        <h4
          className={classNames(
            "mr-1 mb-0",
            props.item.item.isOnPromo ? "" : "no-promo"
          )}
        >
          <p>
            <span>$</span>
            {props.item.item.price}
          </p>
        </h4>
      )}
      <button
        className="btn btn-success btn-position mr-1"
        onClick={props.item.handleClick.bind(this, props.item.item.id)}
        disabled={props.item.item.inCart}
      >
        Add to cart
      </button>
    </div>
  );
}
