import React, { Component } from "react";
import "./CSS/Promotions.css";
import PromotionsRender from "./ComponentsRender/PromotionsRender";
import PogChamp from "../Img/PogChamp.png";

export default class Promotions extends Component {
  render() {
    return (
      <div className="container-fluid bg-secondary">
        <div className="pt-5">
          <h2 className="text-center">Hello to our promotion section</h2>
          <div className="d-flex justify-content-center promo-div">
            <img src={PogChamp} alt="PogChamp" className="img-fluid" />
            <div className="text-center d-flex justify-content-center align-items-center mt-5 promo-text-div">
              <h2 className="mr-4">Only</h2>
              <h4
                id="hot-deals"
                className="text-danger font-weight-bold border border-danger rounded p-2"
              >
                HOT DEALS
              </h4>
            </div>
          </div>
        </div>
        <div>
          <PromotionsRender />
        </div>
      </div>
    );
  }
}
