import React, { Component } from "react";
import LetMeInMeme from "./LetMeInMeme";
import { connect } from "react-redux";
import { addToCart } from "../../Store/Actions/letMeCartActions";

class LetMeInRender extends Component {
  componentWillUnmount() {
    localStorage.setItem("letMeState", JSON.stringify(this.props.letMeData));
  }
  render() {
    const letMeRender = this.props.letMe.map(item => (
      <LetMeInMeme
        key={item.id}
        item={item}
        handleClick={() => this.props.addToCart(item.id)}
      />
    ));
    return (
      <div className="container-fluid">
        <div className="row">{letMeRender}</div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  letMe: state.letMeData
});

const mapDispatchToProps = dispatch => {
  return {
    addToCart: id => {
      dispatch(addToCart(id));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LetMeInRender);
