import React, { Component } from "react";
import { Link } from "react-router-dom";
import Pepe from "../Img/pepe.png";
import "./CSS/Navbar.css";
import classNames from "classnames";

export default class Navbar extends Component {
  constructor() {
    super();
    this.state = {
      menu: false
    };
  }

  handleClick = () => {
    this.setState(prevState => ({
      menu: !prevState.menu
    }));
  };

  render() {
    return (
      <React.Fragment>
        <nav className="navbar navbar-expand-sm fixed-top navbar-dark bg-dark">
          <Link to="/" className="navbar-brand">
            <img
              id="logo"
              src={Pepe}
              alt="logo"
              className="d-inline-block img-fluid align-center mr-2"
            />
            Meme market
          </Link>

          <div className="collapse navbar-collapse">
            <ul className="navbar-nav align-items-center">
              <li className="nav-item">
                <Link to="/" className="nav-link active">
                  Main page
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/promotions" className="nav-link">
                  Promotions
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/products" className="nav-link">
                  Products
                </Link>
              </li>
            </ul>
            <Link to="/cart" className="ml-auto nav-link">
              <button className="btn btn-outline-light">
                My cart
                <i className="fas fa-cart-plus" />
              </button>
            </Link>
          </div>
          <button className="navbar-toggler" onClick={() => this.handleClick()}>
            <span className="navbar-toggler-icon" />
            {console.log(this.state)}
          </button>

          <div
            className={classNames(
              "mobile-div bg-dark text-center",
              this.state.menu ? "menu-show" : ""
            )}
          >
            <ul className="navbar-nav">
              <li className="nav-item mt-3 mb-2">
                <Link
                  to="/"
                  className="nav-link active"
                  onClick={() => this.handleClick()}
                >
                  Main page
                </Link>
              </li>
              <li className="nav-item my-2">
                <Link
                  to="/promotions"
                  className="nav-link"
                  onClick={() => this.handleClick()}
                >
                  Promotions
                </Link>
              </li>
              <li className="nav-item my-2" onClick={() => this.handleClick()}>
                <Link to="/products" className="nav-link">
                  Products
                </Link>
              </li>
            </ul>
            <Link
              to="/cart"
              className="nav-link my-2"
              onClick={() => this.handleClick()}
            >
              <button className="btn btn-outline-light">
                My cart
                <i className="fas fa-cart-plus" />
              </button>
            </Link>
          </div>
        </nav>
      </React.Fragment>
    );
  }
}
