import React from "react";
import "../CSS/Cart.css";

export default function CartRender(props) {
  return (
    <React.Fragment>
      {props.item.inCart ? (
        <div className="col-9 col-sm-7 col-lg-10 d-flex flex-column align-items-center  flex-lg-row align-self-center cart-item border border-info mb-1">
          <div className="m-3">
            <img
              src={props.item.img2}
              alt="MemeImg"
              className="cartImg img-fluid"
            />
          </div>
          <div className="d-flex flex-row mx-auto align-items-center">
            <button
              className=" mx-1 btn"
              onClick={props.addAmount.bind(this, props.item.id)}
              disabled={props.item.amount === 99 ? true : false}
            >
              <i className="fas fa-plus-square" />
            </button>
            <div className="text-center amountDiv">
              <div className="my-2">Amount:</div>
              <div className="my-2">{props.item.amount}</div>
            </div>
            <button
              className="btn  mx-1"
              onClick={props.substractAmount.bind(this, props.item.id)}
              disabled={props.item.amount === 1 ? true : false}
            >
              <i className="fas fa-minus-square" />
            </button>
          </div>
          <div className="d-flex flex-column justify-content-around align-items-center mx-auto priceDiv">
            {props.item.isOnPromo ? (
              <div className="text-center">
                <span className="text-danger">Promotion price: </span>
                <span className="ml-1">$</span>
                {props.item.promotion}
              </div>
            ) : (
              <div>
                Price:
                <span className="ml-1">$</span>
                {props.item.price}
              </div>
            )}
            <div>
              Total:
              <span className="ml-1">$</span>
              {props.item.total}
            </div>
          </div>
        </div>
      ) : null}
    </React.Fragment>
  );
}
