import React, { Component } from "react";
import ListData from "../Components/Products/MemeList/ListData";
import List from "./Products/MemeList/List";
import "./CSS/Products.css";
import { Route, Switch } from "react-router-dom";
import LetMeInRender from "./Products/LetMeIn/LetMeInRender";
import SuprisedPikaRender from "./Products/SuprisedPIka/SuprisedPikaRender";
import FirstWorldRender from "./Products/FirstWorldMeme/FirstWorldRender";
import KermitRender from "./Products/Kermit/KermitRender";

export default class Products extends Component {
  state = {
    listData: ListData
  };
  render() {
    let listRender = this.state.listData.map(item => (
      <List key={item.id} item={item} />
    ));
    return (
      <React.Fragment>
        <div className="container-fluid bg-secondary productDiv">
          <div className="row justify-content-center">{listRender}</div>
          <div>
            <Switch>
              <Route path="/products/letmein" component={LetMeInRender} />
              <Route path="/products/pikachu" component={SuprisedPikaRender} />
              <Route path="/products/kermit" component={KermitRender} />
              <Route path="/products/firstworld" component={FirstWorldRender} />
            </Switch>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
