import React, { Component } from "react";
import SuprisedPika from "./SuprisedPika";
import { connect } from "react-redux";
import { PikachuAddToCart } from "../../Store/Actions/pikachuActions";

class PikeRender extends Component {
  componentWillUnmount() {
    localStorage.setItem("pikaState", JSON.stringify(this.props.pika));
  }

  render() {
    const localPika = localStorage.getItem("pikaState");
    console.log(localPika);
    const pikaRender = this.props.pika.map(item => (
      <SuprisedPika
        key={item.id}
        item={item}
        handleClick={() => this.props.PikachuAddToCart(item.id)}
      />
    ));
    return (
      <div className="container-fluid">
        <div className="row">{pikaRender}</div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  pika: state.pikachuData
});

const mapDispatchToProps = dispatch => {
  return {
    PikachuAddToCart: id => {
      dispatch(PikachuAddToCart(id));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PikeRender);
