import { FIRST_WORLD_ADD_TO_CART } from "../Actions/firstWrldTypes";
import { FIRST_WORLD_ADD_AMOUNT } from "../Actions/firstWrldTypes";
import { FIRST_WORLD_SUBSTRACT_AMOUNT } from "../Actions/firstWrldTypes";
import firstWrldData from "../../Products/FirstWorldMeme/FirstWorldData";

export default function(firstWrldState = firstWrldData, action) {
  switch (action.type) {
    case FIRST_WORLD_ADD_TO_CART:
      console.log({ firstWrldState });
      const product = firstWrldState.find(item => item.id === action.payload);
      const stateIndex = firstWrldState.indexOf(product);
      firstWrldState[stateIndex].inCart = true;
      firstWrldState[stateIndex].amount = 1;
      firstWrldState[stateIndex].total = parseFloat(
        (
          firstWrldState[stateIndex].amount *
          (firstWrldState[stateIndex].isOnPromo
            ? firstWrldState[stateIndex].promotion
            : firstWrldState[stateIndex].price)
        ).toFixed(2)
      );
      return [...firstWrldState];
    case FIRST_WORLD_ADD_AMOUNT:
      const addProduct = firstWrldState.find(
        item => item.id === action.payload
      );
      const addIndex = firstWrldState.indexOf(addProduct);
      firstWrldState[addIndex].amount += 1;
      firstWrldState[addIndex].total = parseFloat(
        (
          firstWrldState[addIndex].amount *
          (firstWrldState[addIndex].isOnPromo
            ? firstWrldState[addIndex].promotion
            : firstWrldState[addIndex].price)
        ).toFixed(2)
      );
      return [...firstWrldState];
    case FIRST_WORLD_SUBSTRACT_AMOUNT:
      const substractProduct = firstWrldState.find(
        item => item.id === action.payload
      );
      const substractIndex = firstWrldState.indexOf(substractProduct);
      firstWrldState[substractIndex].amount -= 1;
      firstWrldState[substractIndex].total = parseFloat(
        (
          firstWrldState[substractIndex].amount *
          (firstWrldState[substractIndex].isOnPromo
            ? firstWrldState[substractIndex].promotion
            : firstWrldState[substractIndex].price)
        ).toFixed(2)
      );
      return [...firstWrldState];
    default:
      return firstWrldState;
  }
}
