import { PIKACHU_ADD_TO_CART } from "../Actions/pikachuTypes";
import { PIKACHU_ADD_AMOUNT } from "../Actions/pikachuTypes";
import { PIKACHU_SUBSTRACT_AMOUNT } from "../Actions/pikachuTypes";
import pikachuData from "../../Products/SuprisedPIka/SuprisedPIkaData";

export default function(pikachuState = pikachuData, action) {
  switch (action.type) {
    case PIKACHU_ADD_TO_CART:
      console.log({ pikachuState });
      const product = pikachuState.find(item => item.id === action.payload);
      const stateIndex = pikachuState.indexOf(product);
      pikachuState[stateIndex].inCart = true;
      pikachuState[stateIndex].amount = 1;
      pikachuState[stateIndex].total = parseFloat(
        (
          pikachuState[stateIndex].amount *
          (pikachuState[stateIndex].isOnPromo
            ? pikachuState[stateIndex].promotion
            : pikachuState[stateIndex].price)
        ).toFixed(2)
      );
      return [...pikachuState];
    case PIKACHU_ADD_AMOUNT:
      const addProduct = pikachuState.find(item => item.id === action.payload);
      const addIndex = pikachuState.indexOf(addProduct);
      pikachuState[addIndex].amount += 1;
      pikachuState[addIndex].total = parseFloat(
        (
          pikachuState[addIndex].amount *
          (pikachuState[addIndex].isOnPromo
            ? pikachuState[addIndex].promotion
            : pikachuState[addIndex].price)
        ).toFixed(2)
      );
      return [...pikachuState];
    case PIKACHU_SUBSTRACT_AMOUNT:
      const substractProduct = pikachuState.find(
        item => item.id === action.payload
      );
      const substractIndex = pikachuState.indexOf(substractProduct);
      pikachuState[substractIndex].amount -= 1;
      pikachuState[substractIndex].total = parseFloat(
        (
          pikachuState[substractIndex].amount *
          (pikachuState[substractIndex].isOnPromo
            ? pikachuState[substractIndex].promotion
            : pikachuState[substractIndex].price)
        ).toFixed(2)
      );
      return [...pikachuState];
    default:
      return pikachuState;
  }
}
