import React from "react";
import "./SuprisedPika.css";
import ProductPrice from "../MemeList/ProductPrice";

function suprisedPikaMeme(props) {
  return (
    <div className="col-12 col-md-6 col-xl-4 mt-5 meme-template d-flex justify-content-center justify-content-md-start">
      <div className="pika-meme">
        <img
          src={props.item.img}
          alt="Img meme"
          className="suprisedPikaImg img-fluid"
        />
        <h6 className="text1">{props.item.text1}</h6>
        <h6 className="text2">{props.item.text2}</h6>
        <h6 className="text3">{props.item.text3}</h6>
        <h6 className="text4">{props.item.text4}</h6>
      </div>
      <ProductPrice item={props} />
    </div>
  );
}

export default suprisedPikaMeme;
