import React, { Component } from "react";
import "./CSS/MainPage.css";
import ListData from "./Products/MemeList/ListData";
import MainPageList from "./Products/MemeList/MainPageList";

export default class MainPage extends Component {
  state = {
    memeList: ListData
  };

  render() {
    const renderList = this.state.memeList.map(item => (
      <MainPageList key={item.id} item={item} />
    ));
    return (
      <React.Fragment>
        <div className="container-fluid bg-secondary d-flex flex-column align-items-center">
          <div className="d-flex flex-column obiDiv mt-2">
            <h1 className="top">Hello there</h1>
            <h1 className="bot">We have memes</h1>
            <img
              src="https://i.gyazo.com/e32d8a75d3a9c7aeb7dd696dc9004c05.png"
              alt="Hello there"
              className="img-fluid"
            />
          </div>
          <div className="mt-3 text-center">
            <h1>Let's start our journey! Click on a meme</h1>
            <div className="row">{renderList}</div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
