import React from "react";
import "./LetMeIn.css";
import ProductPrice from "../MemeList/ProductPrice";

function LetMeInMeme(props) {
  return (
    <div className="col-12 col-md-6 col-xl-4 mt-5 meme-template d-flex justify-content-center justify-content-md-start">
      <div className="letme-meme">
        <img
          src={props.item.img}
          alt="Img meme"
          className="letmeImg img-fluid"
        />
        <h2 className="top1">{props.item.text1}</h2>
        <h2 className="bot1">{props.item.text2}</h2>
        <h2 className="top2">{props.item.text1}</h2>
        <h2 className="bot2">{props.item.text2}</h2>
      </div>
      <ProductPrice item={props} />
    </div>
  );
}

export default LetMeInMeme;
