import { PIKACHU_ADD_TO_CART } from "./pikachuTypes";
import { PIKACHU_ADD_AMOUNT } from "./pikachuTypes";
import { PIKACHU_SUBSTRACT_AMOUNT } from "./pikachuTypes";

export const PikachuAddToCart = id => {
  return {
    type: PIKACHU_ADD_TO_CART,
    payload: id
  };
};

export const PikachuAddAmount = id => {
  return {
    type: PIKACHU_ADD_AMOUNT,
    payload: id
  };
};

export const PikachuAubstractAmount = id => {
  return {
    type: PIKACHU_SUBSTRACT_AMOUNT,
    payload: id
  };
};
