import { KERMIT_ADD_TO_CART } from "./kermitTypes";
import { KERMIT_ADD_AMOUNT } from "./kermitTypes";
import { KERMIT_SUBSTRACT_AMOUNT } from "./kermitTypes";

export const KermitAddToCart = id => {
  return {
    type: KERMIT_ADD_TO_CART,
    payload: id
  };
};

export const KermitAddAmount = id => {
  return {
    type: KERMIT_ADD_AMOUNT,
    payload: id
  };
};

export const KermitAubstractAmount = id => {
  return {
    type: KERMIT_SUBSTRACT_AMOUNT,
    payload: id
  };
};
