import { ADD_TO_CART } from "./types";
import { ADD_AMOUNT } from "./types";
import { SUBSTRACT_AMOUNT } from "./types";

export const addToCart = id => {
  return {
    type: ADD_TO_CART,
    payload: id
  };
};

export const addAmount = id => {
  return {
    type: ADD_AMOUNT,
    payload: id
  };
};

export const substractAmount = id => {
  return {
    type: SUBSTRACT_AMOUNT,
    payload: id
  };
};
