/* eslint-disable array-callback-return */

import React, { Component } from "react";
import LetMeInMeme from "../Products/LetMeIn/LetMeInMeme";
import PikachuMeme from "../Products/SuprisedPIka/SuprisedPika";
import KermitMeme from "../Products/Kermit/KermitMeme";
import FirstWorldMeme from "../Products/FirstWorldMeme/FirstWorldMeme";
import { connect } from "react-redux";
import { addToCart } from "../Store/Actions/letMeCartActions";
import { PikachuAddToCart } from "../Store/Actions/pikachuActions";
import { KermitAddToCart } from "../Store/Actions/kermitActions";
import { FirstWrldAddToCart } from "../Store/Actions/firstWrldActions";

class PromotionsRender extends Component {
  handleClick = id => {
    console.log(id);
  };

  render() {
    const letMeMeme = this.props.letMe.map(item => {
      if (item.isOnPromo === true) {
        return (
          <LetMeInMeme
            key={item.id}
            item={item}
            handleClick={() => this.props.addToCart(item.id)}
          />
        );
      }
    });
    const pikachuMeme = this.props.pika.map(item => {
      if (item.isOnPromo === true) {
        return (
          <PikachuMeme
            key={item.id}
            item={item}
            handleClick={() => this.props.PikachuAddToCart(item.id)}
          />
        );
      }
    });
    const kermitMeme = this.props.kermit.map(item => {
      if (item.isOnPromo === true) {
        return (
          <KermitMeme
            key={item.id}
            item={item}
            handleClick={() => this.props.KermitAddToCart(item.id)}
          />
        );
      }
    });
    const firstWorldMeme = this.props.firstWrld.map(item => {
      if (item.isOnPromo === true) {
        return (
          <FirstWorldMeme
            key={item.id}
            item={item}
            handleClick={() => this.props.FirstWrldAddToCart(item.id)}
          />
        );
      }
    });

    return (
      <div className="container-fluid">
        <div className="row">
          {letMeMeme}
          {pikachuMeme}
          {kermitMeme}
          {firstWorldMeme}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  letMe: state.letMeData,
  pika: state.pikachuData,
  kermit: state.kermitData,
  firstWrld: state.firstWrldData
});

const mapDispatchToProps = dispatch => {
  return {
    addToCart: id => {
      dispatch(addToCart(id));
    },
    PikachuAddToCart: id => {
      dispatch(PikachuAddToCart(id));
    },
    KermitAddToCart: id => {
      dispatch(KermitAddToCart(id));
    },
    FirstWrldAddToCart: id => {
      dispatch(FirstWrldAddToCart(id));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PromotionsRender);
